
.App {
  text-align: center;
  padding: 20px;
}

.priority-board {
  padding: 20px;
}

.priority-columns {
  display: flex;
  justify-content: space-around;
}

.priority-column {
  width: 20%;
  background-color: #f4f4f4;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.ticket-card {
  background-color: #fff;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.ticket-card h4 {
  margin: 0;
  font-size: 16px;
}

.controls button {
  margin: 5px;
  padding: 5px 10px;
  cursor: pointer;
}
